$backgroundColor: #ffffff;
$firstColor: #d8c29d;



body {
  margin: 0;
  padding: 0;
  border-top: 2px solid #d8c29d;
  overflow-x: hidden;
  font-family: Jost;
}

* {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.notification {
  background-color: black;
  padding: 4px;
  text-align: center;
}


.notification p {
  margin: 0;
  color: white;
  line-height: 26px;
  font-size: 13px;
  letter-spacing: 0.03rem;
  font-family: Jost;
  font-weight: 400;
  transform: translateY(100%);
  opacity: 0;
  transition: all .5s ease-in;
}

.notification p.open {
  opacity: 1;
  transform: translateY(0%);
}


h1 {
  font-size: 64px;
  line-height: 64px;
  margin: 12px 0;
  font-family: Baskerville;
  font-weight: 500;
}



h2 {
  margin: 0;
  padding: 10px 0;
  font-size: 36px;
  line-height: 1.4;
  font-family: Baskerville;
  font-weight: 500;
  position: relative;
}


h2:before {
	content: '';
	background-color: #d8c29d;
	height: 1px;
	display: block;
	position: absolute;
	top: 4px;
	left: 0;
  width: 100px;
}


h3 {
  color: #0F0F0F;
  font-size: 24px;
  line-height: 38px;
  margin: 1rem 0;
  font-family: Baskerville;
  font-weight: 500;
}

p {
  color: #606e7c;
  line-height: 26px;
  font-size: 18px;
  margin: 24px 0;
}

img {
  max-width: 100%;
}


ul {
  color: #606e7c;
  line-height: 30px;
  font-size: 20px;
}

button {
  font-family: Baskerville;
  letter-spacing: 0.03rem;
  padding: 16px 36px;
  border: none;
  margin-top: 12px;
  background-color: #E5D7BE;
  color: #846734;
  text-transform: uppercase;
  font-size: 12px;
  cursor: pointer;
}


.wrapper {
  width: 1100px;
  max-width: 90%;
  margin: 3rem auto;
}

@font-face { 
  font-family: 'Baskerville'; 
  src: url('/assets/fonts/LibreBaskerville-Regular.ttf') format('truetype');
  font-weight: 500;
  }

@font-face { 
  font-family: 'Baskerville'; 
  src: url('/assets/fonts/LibreBaskerville-Bold.ttf') format('truetype');
  font-weight: 700;
}
  


#text .wrapper p {
  margin-top: 0;
}



@media only screen and (min-width: 600px) {  

  #text {
    width: 1100px;
    max-width: 90%;
    margin: 0 auto;
  }

  #text .wrapper {
    width: 750px;
    margin: 3rem auto;
  }


}






@media only screen and (min-width: 600px) { 

  h1 {
    font-size: 72px;
    line-height: 87px;
  }

  h2 {
    font-size: 48px;
    line-height: 76px;
  }

  h3 {
    font-size: 30px;
    line-height: 42px;
  }

  p {
    line-height: 30px;
    font-size: 20px;
    margin: 24px 0;
  }

}







a.toggle-nav {
  position: absolute;
  top: 0px;
  right: 48px; }



.mobile-nav {
  z-index: 9999;
  background: #252525;
  padding: 20px 0px;
  width: 50%;
  height: 100vh;
  transition: all .3s ease-in-out;
  transform: translateX(-100%);
  position: absolute;
  left: 0;
}

.mobile-nav-main {
  margin: 0;
  list-style: none;
  padding-left: 0;
  margin: 0 30px;
}

.mobile-nav ul li { 
  padding-top: 1rem;
  font-size: 18px;
}

.mobile-nav ul li a { 
  text-decoration: none;
  color: white;
} 

.mobile-nav.open {
  transform: translateX(0%);
  
}

.mobile-dropdown-nav {
  display: none;
  list-style: none;
  padding-left: 20px;
  border-left: 1px solid #cecece;
  margin: 10px 0px;
}

.mobile-dropdown-nav.open {
  display: block;
}


.carot.open {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}


.carot {
  opacity: .5;
  height: 16px;
  width: 16px;
  float: right;
  margin-top: 3px;
  margin-left: 4px;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

/* Hamburger Menu Icon */

.hamburger {
  display: block;
  overflow: hidden;
  cursor: pointer;
  position: absolute;
  left: 1px;
  top: 6px;
}

.hamburger .line {
  width: 30px;
  height: 3px;
  background-color: #35363a;
  display: block;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}



#hamburger-1.open .line:nth-child(1) { 
  transform: translate(110%);
}

#hamburger-1.open .line:nth-child(2) { 
  transform: translate(0%);
}

#hamburger-1.open .line:nth-child(3) { 
  transform: translate(-110%);
}

.social {
  text-align: center;
}


#insta {
  cursor: pointer;
  padding-top: 12px;
}


@media only screen and (min-width: 600px) { 


  .mobile-nav-main {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}









@media only screen and (max-width: 600px) {


.nav-logo {
  background-color: #ffffff;
  text-align: left;
  padding-left: 32px;
  margin: 12px 0;
}
} /* media query*/



@media only screen and (min-width: 600px) {


  #header {
    max-width: 100%;
    margin: 0 auto;
    border-bottom: 1px solid #D1D1D1;
  } 

  .nav-inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    max-width: 90%;
    margin: 0 auto;
    width: 1500px;
  }

  .first_li {
    position: relative;
  }

  .mobile-dropdown-nav {
    opacity: 0;
    position: absolute;
    overflow: hidden;
    padding: 20px;
    top: -65px;
    border-radius: 2px;
    transition: all 0.5s; 
    transform: translateY(100px);
    will-change: opacity;
    display: none;
    background-color: #d8c29d;
    border-left: none;
  }

  .mobile-dropdown-nav li {
    display: inline-block;
    padding: 10px 10px;
    min-width: 150px;
  }

  .trigger-enter .mobile-dropdown-nav {
    display: block;
  }
  .trigger-enter-active .mobile-dropdown-nav {
    opacity: 1;
  }

  .mobile-nav {
    display: block;
    background-color: transparent;
    width: 100%;
    height: unset;
    transform: translateX(0%);
    text-align: right;
    position: relative;
}

a.toggle-nav {
  display: none; }


.mobile-nav-main li {
  display: inline-block;
  padding: 1rem;
}

.mobile-dropdown-nav li a { 
  font-size: 16px;
  color: white !important;
}

.mobile-nav ul li a { 
  text-decoration: none;
  color: #606e7c;
  font-size: 14px;
  text-transform: uppercase;
} 

.carot.open { 
  transform: none;
}
  
} /* media query*/



/* hero */

#hero {
