#images img {
  height: 200px;
  object-fit: cover;
  width: 100%;
}

.row_img {
  position: relative;
  overflow: hidden;
  display: flex;
}

.row_img_content h3 {
  color: #fff;
}

.row_img_content {
  position: absolute;
  color: white;
  bottom: 0;
  left: 0;
  right: 0;
  height: 112px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  transform: translateY(100%);
  transition: .4s ease-in-out;
  cursor: pointer;
  background-image: linear-gradient(#ffffff00, #38353594);
}

.row_img:hover .row_img_content {
  transform: translateY(0%);
}


.row_img:hover {
  cursor: pointer;
}


